/**
 * Ê†πÊçÆÂàÜÁ±ªËøáÊª§Ê≠åÊõ≤
 * @param {Array} songs - Ê≠åÊõ≤ÂàóË°®
 * @param {string} categoryId - ÂàÜÁ±ªID
 * @returns {Array} ËøáÊª§ÂêéÁöÑÊ≠åÊõ≤ÂàóË°®
 */
export function filterSongsByCategory(songs, categoryId) {
  if (!categoryId || categoryId === '') {
    return songs
  }
  return songs.filter(song => song.categoryId === categoryId)
}

/**
 * È™åËØÅÂΩïÈü≥Êèê‰∫§
 * @param {Object|null} selectedSong - ÈÄâ‰∏≠ÁöÑÊ≠åÊõ≤
 * @param {string} recordingPath - ÂΩïÈü≥Êñá‰ª∂Ë∑ØÂæÑ
 * @returns {Object} È™åËØÅÁªìÊûú { valid: boolean, message: string }
 */
export function validateRecordingSubmission(selectedSong, recordingPath) {
  if (!selectedSong) {
    return { valid: false, message: 'ËØ∑ÂÖàÈÄâÊã©‰∏ÄÈ¶ñÊ≠åÊõ≤' }
  }
  if (!recordingPath || recordingPath === '') {
    return { valid: false, message: 'ËØ∑ÂÖàÂΩïÂà∂ÊÇ®ÁöÑÊ≠åÂ£∞' }
  }
  return { valid: true, message: 'È™åËØÅÈÄöËøá' }
}

